export default {
	data() {
		return {
			phoneData: "",
			passwordData: ""
		}
	},
	onLoad() {

	},
	methods: {
		home() {
			console.log(this.phoneData)
			if (this.phoneData === "123456" && this.passwordData === "123456") {
				uni.switchTab({
					url: '/pages/tabBar/curriculum/curriculum',
					success: res => {},
					fail: () => {},
					complete: () => {}
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '账号或密码错误，请重新输入!',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			}

		}
	}
}
